import { css } from 'lit';

export const styles = css`::slotted(igc-icon){color:var(--icon-color)}:host([variant=primary]){background:var(--background-color);color:var(--text-color)}:host([shape=square]),:host([outlined][shape=square]) [part=base]{border-radius:var(--border-radius)}:host([outlined]) [part=base]{-webkit-box-shadow:inset 0 0 0 .125rem var(--border-color);box-shadow:inset 0 0 0 .125rem var(--border-color)}`;
